html, body {
  font: 14px/1.21 'Helvetica Neue', arial, sans-serif;
  font-weight: 400;
  padding: 0;
  margin: 0;
}

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

h1 {
  text-align: center;
}

.Settings {
  padding: 0.5em 2em;
  background-color: #EEEEEE;
}

.Results {
  padding: 0.5em 2em;
}

.FixtureResult {
  margin: 1em 0;
}

.FixtureResult__iterators {
  display: flex;
  flex-flow: row wrap;
}

.FixtureHeader {
  margin: 1em 0;
}

.FixtureHeader__name {
  font-weight: bold;
  display: inline-block;
  padding-right: 1em;
}

.FixtureHeader__location {
  display: inline-block;
  padding-right: 1em;
}

.FixtureHeader__profile {

}

.IteratorResult {
  text-align: center;
  flex: 0 1 300px;
  min-height: 100px;
  background: #eeeeee;
  margin: 2px;
  padding: 10px;
}

.IteratorResult--error {
  background: #ffaaaa;
}

.IteratorResult--error {

}

.IteratorResult__metric {
}

.IteratorResult__metric--big {
  font-size: 2em;
  font-weight: bold;
}

.IteratorResult__metric--middle {
  font-size: 1.5em;
}

.IteratorResult__metric--small {
    font-size: 1em;
}

.IteratorResult__metric--warn {
  color: #cc3333;
}

.IteratorResult__metric--hide {
  display: none;
}

html .react-spinner_bar {
  background-color: black;
}

html .react-spinner {
  width: 128px;
  height: 128px;
  position: absolute;
}

.Summary {
  font-size: 12px;
  margin-bottom: 2em;
  border-spacing: 0px;
}

.Summary td {
  padding: 2px;
}

.Summary__sourceName {
  text-align: left;
  width: 160px;
}

.Summary__count {
  text-align: center;
  width: 60px;
}

.Summary__itemUrl {
  text-align: left;
  max-width: 900px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis
}

.Summary__line:nth-child(even) {
  background-color: #EEEEEE;
}


